import React from "react";
import { css } from "@emotion/react";
import { Link, graphql } from "gatsby";
import { rhythm } from "../utils/typography";
import Layout from "../components/layout";
//import "highlight.js/styles/atom-one-dark.css";
//import "highlight.js/styles/atom-one-light.css";

const LectureIndex = ({ data }) => {
  const { edges: lectures } = data.allMdx;
  const [lightMode, setLightMode] = React.useState(
    typeof window != "undefined" && sessionStorage.getItem("lightMode") != null
  );

  function changeTheme() {
    if (sessionStorage.getItem("lightMode")) {
      sessionStorage.removeItem("lightMode");
      setLightMode(false);
    } else {
      sessionStorage.setItem("lightMode", "true");
      setLightMode(true);
    }
  }

  return (
    <Layout>
      <div>
        <h1
          css={css`
            display: inline-block;
            border-bottom: 1px solid;
          `}
        >
          {data.site.siteMetadata.title}
        </h1>
        <Link
          to="https://www.hs-furtwangen.de/personen/profil/2647-stephanieheintz/"
          css={css`
            display: block;
            font-size: 1.3em;
            margin-top: -0.5em;
          `}
        >
          {"Prof. Dr. Stephanie Heintz"}
        </Link>
        <input
          type="checkbox"
          checked={lightMode}
          onChange={changeTheme}
          css={css`
            margin-right: ${rhythm(1 / 4)};
          `}
        />
        <label>Lightmode</label>
        {lectures.map(({ node: lecture }) => (
          <div key={lecture.id}>
            <Link
              to={lecture.fields.slug + "0"}
              css={css`
                text-decoration: none;
                color: inherit;
              `}
            >
              <h3
                css={css`
                  margin-bottom: ${rhythm(1 / 4)};
                `}
              >
                {lecture.frontmatter.title}{" "}
                <span
                  css={css`
                    color: #555;
                  `}
                >
                  â€” {lecture.frontmatter.date}
                </span>
              </h3>
            </Link>
          </div>
        ))}
      </div>
    </Layout>
  );
};
//<h4>{data.allMdx.totalCount} Stunden</h4>
//<p>{lecture.excerpt}</p>

export const pageQuery = graphql`
  query lectureIndex {
    allMdx(sort: { fields: [frontmatter___date], order: ASC }) {
      totalCount
      edges {
        node {
          id
          excerpt
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY")
          }
          fields {
            slug
          }
        }
      }
    }
    site {
      siteMetadata {
        title
      }
    }
  }
`;

export default LectureIndex;
